/* src/pages/TableView.css */
.table-view-wrapper {
    display: flex;
    min-height: 100vh;
    width: 100%;
}

.table-actions {
    width: 15vw; /* Было 15.625rem (250px), теперь 15vw */
    background-color: #f8f9fa;
    padding: 1.5vh; /* Было 1.25rem (20px), теперь 1.5vh */
    border-right: 0.05vw solid #dee2e6; /* Было 0.0625rem (1px), теперь 0.05vw */
}

.table-actions h3 {
    margin-top: 0;
    margin-bottom: 1vh; /* Было 0.9375rem (15px), теперь 1vh */
    font-size: 1vw; /* Было 1.125rem (18px), теперь 1vw */
}

.action-btn {
    display: block;
    width: 100%;
    padding: 0.5vh; /* Было 0.625rem (10px), теперь 0.5vh */
    margin-bottom: 0.5vh; /* Было 0.625rem (10px), теперь 0.5vh */
    border: 0.05vw solid #ccc; /* Было 0.0625rem (1px), теперь 0.05vw */
    border-radius: 0.2vw; /* Было 0.25rem (4px), теперь 0.2vw */
    background-color: white;
    cursor: pointer;
    text-align: left;
    transition: background-color 0.2s;
}

.action-btn:hover {
    background-color: #e9ecef;
}

.table-content {
    flex: 1;
    padding: 1.5vh; /* Было 1.25rem (20px), теперь 1.5vh */
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 70vw; /* Было 84.375rem (1350px), теперь 70vw */
}

.table-content h2 {
    margin-bottom: 1vh; /* Было 0.9375rem (15px), теперь 1vh */
}