/* src/components/TableRow.css */
.cell-content {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 8vw;
    height: 2vh;
    line-height: 2vh;
}

.edit-input {
    width: 100%;
    height: 2vh;
    padding: 0 0.2vw;
    border: 0.05vw solid #ccc;
    border-radius: 0.1vw;
    font-size: 0.625vw; /* Увеличиваем с 0.5vw до 0.625vw (0.5 * 1.25) */
    box-sizing: border-box;
}